.otherDetails {
	border-radius: 4px;
	padding: 8px 16px;
	margin: 16px 0 32px 0;
	border: 1px solid var(--border-color-1);
	background: var(--bg-white);
}

.otherDetailsInfo {
	display: flex;
	align-items: center;
	padding: 8px 0;
}

.otherDetailsInfo1 {
	padding: 8px 0;
}

.otherDetailsInfoLabel {
	width: 50%;
}

.otherDetailsInfoValue {
	width: 50%;
	text-transform: capitalize;
}

.otherDetailsInfoValue1 {
	text-transform: capitalize;
}

.actionBox {
	border: 1px solid var(--border-color-1);
	background: var(--bg-white);
	border-radius: 4px;
	cursor: pointer;
	font-weight: 500;
	padding: 16px;
	justify-content: space-between;
	margin-bottom: 12px;
}

.location__map {
	background: var(--bg-white);
	border-radius: 4px;
	height: 170px;
}

.actionBoxDarkMode {
	cursor: default;
}